import React from 'react'
import { Link } from 'react-router-dom';

import Web3 from "web3";
import NftJson from "../abis/NftJson.json";

import { useWeb3Context } from "../hooks/web3";

export default function Header(props) {

    const { connect, disconnect, connected, address } = useWeb3Context();


    return (
        <div className="header" style={{ backgroundColor: "#97d7db", backgroundSize: "contain" }}>
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <a href="index.html" className="logo"> <img src="images/unknown.png" width="100" height="100" alt=""/></a>

                        <div className="h-main">
                            {connected
                                ?
                                <button onClick={disconnect} className="wallet-btn">{`${address.substring(0, 5)}...${address.substring(address.length - 5, address.length)}`}</button>
                                :
                                <button onClick={connect} className="wallet-btn">Connect Wallet</button>
                            }
                            <div className="social">
                                <ul>

                                    <li><a href="https://twitter.com/BabyBullClubNFT"><img src="images/twitter.svg" alt=""/></a></li>
                                    <li><a href="https://discord.gg/XDjxjwVd"><img src="images/discord.svg" alt=""/></a></li></ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>



    )
}
