import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

import Web3 from "web3";
import NftJson from "../abis/NftJson.json";

import { useWeb3Context } from "../hooks/web3";


import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';


export default function Main(props) {

    const { connect, disconnect, connected, providerChainID, checkWrongNetwork, address, provider } = useWeb3Context();

    const [nftContract, setNftContract] = useState(null);
    const [mainLoading, setMainLoading] = useState(false);
    const [totalRemained, setTotalRemained] = useState('?');
    const [numberMint, setNumberMint] = useState(1);
    const [mintCost, setMintCost] = useState(90)
    const [noticeToShow, setNoticeToShow] = useState("Please connect wallet to Mint NFT");


    const incNumber = () =>
        numberMint > 19 ? setNumberMint(numberMint) : setNumberMint(numberMint + 1);
    const decNumber = () =>
        numberMint > 1 ? setNumberMint(numberMint - 1) : setNumberMint(numberMint);

    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, []);

    useEffect(() => {
        if (connected) {
            if (providerChainID == 137) {
                loadData();
            } else {
                checkWrongNetwork()
            }
        }
    }, [connected]);

    const _connectToAccount = async () => {
        if (!connected) {
            setNoticeToShow('Connecting...');
            connect()
        }
    }



    const loadData = async () => {
        setMainLoading(true);
        const web3 = new Web3(provider);

        window.web3 = web3;
        const networkAddress = '0x2D771fBD306ec1eB549302Dc6fC36E7F9Ae48A89';
        const _nftContract = await new web3.eth.Contract(NftJson.abi, networkAddress);
        setNftContract(_nftContract)
        setMintCost(90);
        const totalSupply = await _nftContract.methods.totalSupply().call();
        setTotalRemained(4444 - totalSupply.toString());

        setNoticeToShow('Connected, Ready to mint NFT');
        setMainLoading(false);
    }


    const mintNFT = async () => {
        if (connected) {
            setNoticeToShow('Minting, Please Wait...');
            setMainLoading(true);

            await nftContract.methods
                .mint(numberMint)
                .send({ from: address, value: window.web3.utils.toWei((mintCost*numberMint).toString() , "ether") })
                .on('confirmation', (confirmationNumber, receipt) => {
                    console.log(receipt);
                    loadData();
                })
                .on('error', function (error, receipt) {
                    console.log(error);
                    setNoticeToShow(error.message);
                    loadData();
                });
        }
    };



    return (

        <>

            <section className="banner-sec" style={{ backgroundColor: "#97d7db" }}>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="banner-img">
                                <img src="images/final.gif" alt="" />
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="banner-text">
                                <h1 style={{ backgroundColor: "#001aff", textAlign: "center" }}>Baby Bull Club
                                </h1>

                                <p style={{ color: "black" }}>
                                    Baby Bull Club is a community-driven collectibles NFT project.  Baby Bull Club are made up of over 100 exciting traits of faces, hair, hats, and backgrounds. Each  Baby Bull is a unique, non-fungible token (NFT) on the Polygon blockchain.  Weâ€™ve made it our mission to create long-term benefits for our collectors.  Our approach to this has been to ensure that the collection evolves over time. </p>


                                <div className="mint-solpup-main">
                                    <h3 style={{ color: "black" }}>MINT Baby Bull Club
                                        Soon!</h3>
                                    <p style={{ color: "black" }}>{totalRemained} remaining / {mintCost} Matic per Meta Hamsters
                                    </p>
                                    <div className="min-qty-main">
                                        <div className="control">
                                            <button className="bttn bttn-left" onClick={decNumber} id="minus"><i className="fa fa-minus" aria-hidden="true"></i></button>
                                            <input type="number" className="input" id="input" value={numberMint} />
                                            <button className="bttn bttn-right" onClick={incNumber} id="plus"><i className="fa fa-plus" aria-hidden="true"></i></button>
                                        </div>
                                        <a className="mint-now-btn" aria-setsize="150" href="#">Sale: TBD</a>
                                        <br />
                                        {connected
                                            ?
                                            <button onClick={mintNFT} className="mint-now-btn-full" >Mint Now</button>
                                            :
                                            <button onClick={_connectToAccount} className="mint-now-btn-full" >Connect Wallet</button>
                                        }
                                        <a className="mint-now-btn-full" href="https://discord.gg/XDjxjwVd">Join Our Discord!</a>
                                        <div className="clear">
                                            <p>{noticeToShow}</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section class="mission-sec" style={{backgroundColor: "#97d7db"}}>
                <div class="container">
                    <div class="row">
                        <div class="col-lg-12">
                            <div class="mission-txt">
                                <h1 style={{backgroundColor: "#001aff"}}>What Are Baby Bull Club?</h1>
                                <br />
                                <br />
                                <p>Holding a Baby Bull Club allows you to be a part of an exclusive members only Chat coming
                                    soon 2Q 2022, we will also be having a $10,000 Non-Profit Treasury that will be chosen by
                                    the discord community once we sell out of 50% of mint.</p>
                                <p>Baby Bull Club will soon own a NFTworld that will soon be the headquarters For All Baby Bulls
                                    and only Baby Bull Club members.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </>
    )
}
